import { ComponentFixture, TestBed } from '@angular/core/testing';
import { Button2 } from './button-2';

fdescribe('Button2', () => {
  let component: Button2;
  let fixture: ComponentFixture<Button2>;


  beforeEach(async () => {
    await TestBed.configureTestingModule({
      imports: [Button2]
    }).compileComponents();

    fixture = TestBed.createComponent(Button2);
    component = fixture.componentInstance;
    fixture.detectChanges();
  });


  it('should create button-2', () => {
    expect(component).toBeTruthy();
  })

  it('should display label when no label is provided', () => {
    fixture.detectChanges();
    expect(component.label()).toBe('')
  })

  it('should display label when label is provided', () => {
    fixture.componentRef.setInput('label', 'go to contact');
    fixture.detectChanges();
    expect(component.label()).toBe('go to contact')
  });

  it('should emit goto contact when change page is called', () => {
    let emittedValue: any;

    component.goToContact.subscribe(value => {
      emittedValue = value
    })

    component.changePage();

    expect(emittedValue).toBe('/contact');
  })
});